@use "../settings" as *;
@use "../helper" as *;
@use "../tools" as *;

.user {
    display: flex;
    align-items: center;

    cursor: pointer;

    &__wrapper {
        display: flex;
        flex-direction: column;
        align-items: flex-end;

        margin-right: rem(10);

        p {
            color: setcolor('secondary');
            margin: 0;

            &:first-of-type {
                @extend .u-f--h4, .u-f--bold;
            }
        }


    }
}

.userNoIcon {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    text-align: right;

    p:first-of-type {
      @extend .u-f--h3, .u-f--bold;
    }
    p:last-of-type {
        @extend .u-f--h4;
      color: setcolor('secondary');
    }
  }